{/* Digital Marketing Card */}
<Card
  title="Digital Marketing"
  description="From SEO and PPC to email and content, we tailor strategies that drive results."
>
  <div className="relative w-full h-28 mt-4">
    <img
      src="/icons/seo.svg"
      alt="SEO"
      className="absolute top-[0.25rem] left-[1.5rem] w-16 h-6"
    />
    <img
      src="/icons/emailmarketing.svg"
      alt="Email Marketing"
      className="absolute top-[2.1rem] left-[6.6rem] w-[120px] h-[28px]"
    />
    <img
      src="/icons/googleadds.svg"
      alt="Google Ads"
      className="absolute top-[0.6rem] right-[0.5rem] w-[120px] h-[28px]"
    />
    <img
      src="/icons/paidads.svg"
      alt="Paid Advertising"
      className="absolute bottom-[-1.2rem] left-[1rem] w-[120px] h-[28px]"
    />
  </div>

  {/* Increased Growth Image Size */}
  <img
    src="/icons/growth.svg"
    alt="Growth Chart"
    className="w-full max-w-[220px] mx-auto mt-4"
  />
</Card>

{/* Social Media Management Card */}
<Card
  title="Social Media Management"
  description="More than matchmaking! We script, plan, and scale creator partnerships with real impact."
>
  <div className="relative w-full h-[220px] mt-2">
    <motion.div
      className="absolute inset-0 flex items-center justify-center w-full h-full"
      animate={{ rotate: 360 }}
      transition={{ repeat: Infinity, duration: 20, ease: 'linear' }}
    >
      {/* Orbit background */}
      <img
        src="/icons/Group 35518.svg"
        alt="Orbit Background"
        className="absolute w-[180px] h-[180px]"
      />

      {/* Centered Icons */}
      <img
        src="/icons/fbicon.svg"
        alt="Facebook"
        className="absolute top-0 left-1/2 transform -translate-x-1/2 w-[28px] h-[28px]"
      />
      <img
        src="/icons/instaimg.svg"
        alt="Instagram"
        className="absolute right-0 top-1/2 transform -translate-y-1/2 w-[28px] h-[28px]"
      />
      <img
        src="/icons/youtube.svg"
        alt="YouTube"
        className="absolute bottom-0 left-1/2 transform -translate-x-1/2 w-[28px] h-[28px]"
      />
      <img
        src="/icons/tiktok.svg"
        alt="TikTok"
        className="absolute left-0 top-1/2 transform -translate-y-1/2 w-[28px] h-[28px]"
      />
    </motion.div>
  </div>
</Card>
